.main {

    .fotos {
        margin-bottom: 70px;

        .thumb {
            img{
                border-radius: 3px;
                width: 100%;
                vertical-align: top;
            }
        }
        .titulo {
            margin-top: 70px;
            margin-bottom: 40px;
            font-size: 32px;
            line-height: 32px;
            font-weight: 300;
            &.azul {
                color: #4facd3;
            }
            &.naranja {
                color: #ddb146;
            }
        }
        .parrafo {
            line-height: 28px;
        }
        .derecha{
            text-align: right;
        }
    }
}